.content {
    display: grid;
    grid-template-columns: 1fr 2fr;
    grid-template-rows: auto auto;
    grid-template-areas: 'photo about'
    'posts posts';
    row-gap: 15px;
    
}


.about_me_info {
    grid-area: about;
    padding: 10px;
    margin: 10px;
    justify-content: start;

}

.full_name {
    text-align: start;
    font-weight: bold;

}

.editButton {
    justify-self: end;
}

.lookingJob {
    margin-top: 10px;
}

.checkImg {
    width: 15px;
}

.jobDesctiption {
    margin-top: 10px;
}