.avatar {
    grid-area: photo;
    cursor: pointer;
    width: 300px;
    height: 300px;
    margin: 20px;
    position: relative;
}

.inputFile {
    cursor: pointer;
    position: absolute;
    top: 255px;
    display: none;
    background:rgba(255,255,255, 0.7);
}

.avatar:hover .inputFile {
    display: block;
}

.avaPreloader {
    background-color: rgba(215, 215, 215, 0.477);
    display: flex;
    height: 300px;
    margin: 20px;
    width: 300px;
    justify-content: center;
    /* padding-top: 60px; */
}