.App-header {
    background-color: rgb(38, 40, 41);
    grid-area: h;
    font-size: calc(10px + 2vmin);
  }

  .App-logo {
    height: 100%;
    pointer-events: none;
  }
  
  @media (prefers-reduced-motion: no-preference) {
    .App-logo {
      animation: App-logo-spin infinite 20s linear;
    }
  }

  .loginBlock {
    float: right;
    margin: 10px;
  }
  
  .loginBlock a {
    color: white;
  }