.nav {
  grid-area: nav;
  background-color: rgba(244, 239, 239, 0.957);
  padding: 10px;
  align-items: center;
  align-self: stretch;

}

.item {
  color: rgb(24, 15, 15);
  text-decoration: none;
  font-size: 20px;
  margin: 10px;

}

.sct {
  margin-bottom: 5px;
  margin-left: 10px;
}

.item:active {
  color: rgba(255, 249, 249, 0.957);
}

.sct:hover {
  box-shadow: 0 5px 2px rgba(0, 0, 0, 0.4);
  background-color: rgba(185, 215, 237, 0.5);
  border-radius: 10px;
}

@media screen and (max-width: 440px) {
  .nav {
    width: 100vw;
    padding-top: 10vh;
  }
  .item {
    font-size: 2em;
    line-height: 1.5em;
    padding-left: 5px;
  }
  .sct {
    box-shadow: 0 5px 2px rgba(0, 0, 0, 0.4);
    background-color: rgba(185, 215, 237, 0.5);
    padding: 10px;
    border-radius: 10px;
    margin-bottom: 5vh;
  }
}